import React from 'react';

const TextCard = ({title,linkObj,children})=>(
    <div className={"textCard"}>
        <h3>{title}</h3>
        <div className={'des'}>
            {children}
        </div>
        {
            linkObj?
                <a href={linkObj.url}>{linkObj.text}</a>
                :
                <div></div>
        }
    </div>
);
export default TextCard;
