import React from 'react';

const getStyle=(columnNub,align,background,height)=>{
    const style = {
        flexFlow:"row",
        backgroundColor:"white",
        minHeight: height||"180px"
    }
    if(align === "right"){
        style.flexFlow = "row-reverse"
        style.margin = "60px"
    }
    if(align === "center"){
        style.justifyContent="center"
    }
    if(/\.jpg|\.png/.test(background)){
        style.backgroundImage ="url("+background+")";
        style.backgroundColor = undefined;
        style.padding = "100px 80px";
    }else {
        style.backgroundColor = background;
    }
    /*if(background==="black")
        style.color = "white"*/

    if(columnNub){
        style.flex=columnNub;
    }
    return style;
}

const PageSection = ({title,linkObj,columnNub,align,background,height,children})=>(
    <section>
        <h2>{title || ""}</h2>
        <div className={'main'} style={getStyle(columnNub,align,background,height)}>
            {children}
        </div>
    </section>
);
export default PageSection;
