import React, { Component } from 'react';

import { LazyLoadImage } from 'react-lazy-load-image-component';


class ImageSlider extends Component {
    constructor(props) {
        super(props);
        this.state = {
            current:0,
            top:0,
            className:"active"
        };
        this._mountTimer = this._mountTimer.bind(this)
    }


    componentWillReceiveProps(nextProps) {
        //this.state.pics = nextProps.pics
    }


    componentDidMount() {
        console.log(typeof this.props.pics)
        //const app = this;
        /*window.addEventListener("scroll",(e)=>{
            let imgOff = -1 * 360 * document.documentElement.scrollTop / document.documentElement.scrollHeight;
            app.setState({top:imgOff})
            console.log("---scroll"+imgOff)
        })*/

        //document.querySelector(".image-slider img")//.style.top = imgOff;


        const pics = this.props.pics;
        if(pics && (typeof pics !== 'string') && pics.length>1){
            this._mountTimer(pics.length)
        }
    }

    _mountTimer(length){
        const app = this;

        window.setInterval(function () {

            const index = app.state.current === length-1? 0 : app.state.current+1;
            app.setState({current:index})
            console.log('inddd--------'+index)
        },5000)
    }

    render() {
        const pics = this.props.pics;
        if(!pics) {
            return ("");
        }

        if(typeof pics === 'string'){
            return (
                <div className="image-slider">
                    <LazyLoadImage
                        alt={"Loading"}
                        height={600}
                        effect="blur"
                        src={pics} // use normal <img> attributes as props
                        width={"100%"}
                    />
                </div>
            );
        }


        return (
            <div className="image-slider">
                {/*<img src={pics[this.state.current]}/>*/}
                <LazyLoadImage
                    alt={"Loading"}
                    height={600}
                    effect="blur"
                    src={pics[this.state.current]} // use normal <img> attributes as props
                    width={"100%"}
                   // className={this.state.className}
                />
                {/*{
                    pics.map((p,idx)=>
                        <LazyLoadImage
                            key={idx}
                            alt={"Loading"}
                            height={360}
                            effect="blur"
                            src={p} // use normal <img> attributes as props
                            width={"100%"}
                            className={idx === this.state.current? "active":""}
                            style={{top:this.state.top+"px"}}
                        />
                        // <img key={idx} src={p} className={idx === this.state.current? "active":""} style={{top:this.state.top+"px"}}/>
                    )
                }*/}
            </div>
        );
    }
}
export default ImageSlider;
