import React from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';

const BigImgCard = ({title,linkObj,img,children})=>(
    <div className={"bigImgCard"}>
        <LazyLoadImage
            alt={"Loading"}
            width={580}
            height={300}
            effect="blur"
            src={img} // use normal <img> attributes as props
            width={"100%"}
            className={"img"}
        />
        <div className="text">
            <h3>{title}</h3>
            <div className={'des'}>
                {children}
            </div>
            {
                linkObj?
                    <a href={linkObj.url}>{linkObj.text}</a>
                    :
                    <div></div>
            }
        </div>
    </div>
);
export default BigImgCard;
