import React from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';

const SmallImgCard = ({title,url,smallTitle,img,children})=>(
    <a  href={url} className={"smallImgCard"}>
        <LazyLoadImage
            alt={"Loading"}
            width={380}
            height={240}
            effect="blur"
            src={img} // use normal <img> attributes as props
            className={"img"}
        />
        <div className="small">{smallTitle || ""}</div>

        <div className={"title"}>{title}</div>
    </a>
);
export default SmallImgCard;
